.OuterChatListContainer{
    // position:fixed;
    // right: 20px;
    // bottom: 0;
    // z-index: 999;
    .slide-trigger-wrapper{
        display: flex;
        width: 48px;
    }
    .SlideToggleContainer{
        display: flex;
        flex-direction: column;
        align-items: flex-end;
    }
}
.chat-item{
    display: flex;
    align-items: center;
    cursor: pointer;
    padding: 6px 14px;
    &:hover{
        background-color: #f5f5f5;
    }
    .profile{
        border-radius: 20px;
        padding: 3px;
        max-width: 36px;
        margin-right: 14px;
        overflow: hidden;
    }
    svg{
        display: block;
        width: 100%;
        height: auto;
    }
}
.online-indicator{
    font-size: 0;
    background-color: #b5bcc3;
    width: 10px;
    height: 10px;
    border-radius: 10px;
    display: block;
    margin-left: auto;
    &.online{
        background-color: #19aa59;
    }
}
.chat-list{
    width: 280px;
    background-color: #fff;
    .user-name{
        font-weight: 700;
        font-size: 16px;
    }
    .user-description{
        margin-top: 0;
        font-size: 12px;
    }
}
.chat-list-name-and-desc{
    display: flex;
    flex-direction: column;
}

.chat-list-wrapper{
    
    .chat-header{
        padding: 4px 8px;
        border-top-left-radius: 8px;
        border-top-right-radius: 8px;
        overflow: hidden;
        background-color: #007B55;
        color: #fff;
        font-weight: 700;

    }
    .chat-list{
        border: 1px solid #ccc;
        border-top: 0;
    }
}