@mixin wideUp {
    @media (min-width: 1730px) {
        @content;
    }
}
@mixin desktopUpPlus {
    @media (min-width: 1400px) {
        @content;
    }
}
@mixin desktopUp {
    @media (min-width: 1300px) {
        @content;
    }
}
@mixin laptopUp {
    @media (min-width: 1100px) {
        @content;
    }
}
@mixin mobileUp {
    @media (min-width: 590px) {
        @content;
    }
}

.AuthContainer{
    @include laptopUp{
        display: flex;
        align-items: stretch;
        justify-content: center;
    }
}

.login-form.form-container{
    margin-bottom: 40px;
}
.login-page-info-wrapper,
.auth-form-wrapper-container{
    width: 100%;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    @include laptopUp{
        width: 50%;
        min-height: 100vh;
    }
}
.auth-form-wrapper-container{
    background-image: url('../../Assets/login-form-bg.svg');
    padding: 80px 20px;
    min-height: 100vh;
    @include mobileUp{
        padding: 80px 40px;
    }
    @include desktopUpPlus{
        padding-top: 10%;
    }
}
.login-page-info-wrapper{
    position: relative;
    background-image: url('../../Assets/login-elements-bg.svg');
    padding: 40px 20px;
    @include mobileUp{
        padding: 80px 40px;
        padding: 60px;
    }
    @include desktopUpPlus{
        padding-top: 13%;
    }
}

