.form-wrapper{
    text-align: center;
    .title{
        font-weight: 700;
        font-size: 42px;
        color: #00AB55;
        text-align: left;
        margin: 0 0 20px 0;
    }
}
.logo-container{
    margin: 0 auto;
    max-width: 457px;
    width: 100%;
    padding: 20px;
    svg{
        display: block;
        width: 100%;
        height: auto;
    }
}
.info-section-1,
.info-section-2{
    background-color: #fff;
    border-radius: 16px;
    box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);   
}
.login-page-info-container{
    position: relative;
    width: 100%;
    max-width: 450px;
    margin: 0 auto;
    @include laptopUp{
        max-width: 711px;
        margin: 0 auto 0 0;
    }
    @include desktopUpPlus{
        padding-top: 270px;
    }
}
.info-section-1{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    width: 100%;
    max-width: 260px;
    height: 260px;
    padding: 40px;
    margin: 0 auto 40px;
    @include desktopUpPlus{
        position: absolute;
        top: 0;
        right: 0;
        z-index: 1;
        margin: 0;
        max-width: 320px;
        height: 320px;
    }
    .app-name{
        font-weight: 700;
        font-size: 36px;
        line-height: 18px;
        text-transform: uppercase;
        color: #00AB55;
    }
    .logo-wrapper{
        max-width: 80px;
        width: 100%;
        margin-bottom: 30px;
        @include desktopUpPlus{
            max-width: 90px;
        }
        svg{
            width: 100%;
            display: block;
            height: auto;
        }
    }
}
.info-section-2{
    width: 100%;
    padding: 40px 40px 50px;
    @include desktopUpPlus{
        max-width: 473px;
    }
    p{
        font-weight: normal;
        font-size: 16px;
        line-height: 1.5;
        color: #637381;
        @include mobileUp{
            font-size: 18px;
        }
    }
}
.features-info-list{
    font-weight: 700;
    font-size: 36px;
    line-height: 1.5;
    text-transform: uppercase;
    color: #00AB55;
    position: relative;
    padding-bottom: 20px;
    border-bottom: 4px solid #cdedd7;
    margin-bottom: 20px;
    @include mobileUp{
        font-size: 52px;
        line-height: 1.3;
    }
}