
.TodoContainer{
    position: relative;
    display: block;
    padding: 20px 0 9px;
    border-bottom: 1px solid #E7E7E7;
    background-color: #fff;
    transition: all 300ms ease;
    &:hover{
        background-color: rgba(0, 133, 119, 0.1);
    }
    .name{
        font-weight: 600;
        font-size: 14px;
        display: flex;
        align-items: center;

        color: #637381;
    }
    .TodoMoreOptionsContainer{
        position: absolute;
        z-index: 2;
        right: 0;
        top: 10px;
    }
    .actions-container{
        position: relative;
        z-index: 2;
        margin-top: 6px;
        display: flex;
        justify-content: flex-end;
        button{
            margin-left: 0;
        }
    }
}

.todo-name{
    font-size: 14px;
    word-break: break-word;
    cursor: pointer;
    color: #00AB55;
}



.todo-more-trigger{
    margin: 4px 0 0;
    font-weight: 600;
    font-size: 12px;
    color: #637381;
    cursor: pointer;
    &:hover{
        color: #00AB55;
    }
}

.tags{
    display: flex;
    align-items: center;
    justify-content: flex-start;
    margin-bottom: 14px;
    li{
        margin-right: 10px;
    }
}
.tag{
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 7px;
    height: 22px;
    border-radius: 4px;
    font-weight: 600;
    font-size: 12px;
    background: rgba(99, 115, 129, 0.16);
    letter-spacing: 0.25px;
    cursor: help;
    .text{
        line-height: 1;
    }

    &.priority{
        color: #B78103;
        background: rgba(255, 193, 7, 0.16);
    }
    &.type{
        color: #00ab55;
        background: rgba(0, 171, 85, 0.08);
    }
    .icon-left{
        margin-right: 5px;
        padding-bottom: 1px;
    }
    .type-icon-container{
        display: flex;
        svg + svg{
            margin-left: 4px;
        }
    }
}