.TaskEstimatesPlanContainer{
    padding: 20px;
}
.estimate-row-data{
    input{
        font-size: 16px;
        width: 70px;
        padding: 5px 8px;
        border: 1px solid #ccc;
    }
}
.work-time-data{
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
}
.time-schedule,
.work-availability{
    width: calc(50% - 15px);
}
.val{
    font-weight: 700;
}
.planning-action-container{
    display: flex;
    align-items: center;
    justify-content: flex-end;
    button{
        margin-left: 14px;
    }
}
.user-time-schedule{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 20px;
}
.planning-input-wrapper{
    margin-bottom: 26px;
}