.ChatAreaContainer{
    position: fixed;
    right: 0;
    bottom: 0;
    display: flex;
    align-items: flex-end;
    z-index: 9;
    padding-right: 30px;
    .ChatBoxContainer{
        height: 400px;
        width: 300px;
        background-color: #fff;
    }
    .user-icon{
        width: 20px;
        border-radius: 100%;
        margin-right: 10px;
        svg{
            display: block;
            width: 100%;
            height: auto;
        }
    }
    .ChatBoxContainer{
        box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
        margin-right: 3px;
    }

    .conversation-container {
        height: 285px;
        padding-bottom: 0;
        display: flex;
        flex-direction: column;
    }

}