@mixin tabletUp {
    @media (min-width: 768px) {
        @content;
    }
}
@mixin laptopUp {
    @media (min-width: 1100px) {
        @content;
    }
}
.MomentsViewContainer{
    padding: 20px;
    .moments-list{
        max-width: 100%;
        margin: 0;
        border-top: 1px solid #ccc;
        padding-top: 20px;
        @include laptopUp{
            max-width: calc(100% - 460px);
            border: 1px solid #ccc;
            padding: 20px;
        }
    }
    .MomentListContainer{
        @include laptopUp{
            height: calc(100vh - 170px);
            overflow: scroll;
        }
    }
}


