.moment-choices{
    list-style: none;
    display: flex;
    align-items: center;
    padding-left: 0;
    svg{
        display: block;
        path{
            transition: color 300ms ease;
        }
    }
    li{
        margin-left: 15px;
        cursor: pointer;
        &.active,
        &:hover{
            svg path{
                fill: rgb(0, 171, 85);
            }
        }
    }

}
.submit-moment{
    svg{
        color: rgb(0, 171, 85);
    }
}