@mixin wideUp {
    @media (min-width: 1730px) {
        @content;
    }
}
@mixin desktopUp {
    @media (min-width: 1300px) {
        @content;
    }
}

@mixin laptopUp {
    @media (min-width: 1100px) {
        @content;
    }
}
@mixin tabletUp {
    @media (min-width: 768px) {
        @content;
    }
}
@mixin mobileUp {
    @media (min-width: 590px) {
        @content;
    }
}
.calendar-and-todo-wrapper{
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    // padding: 20px;
    // background-color: rgb(240, 240, 240);
}

.route-wrapper{
	flex: 100% 0 0;
    margin: 45px 0 0 auto;
    @include tabletUp{
        margin: 0 0 0 auto;
        flex: calc(100% - 215px) 0 0;
    }
}
.calendar-wrapper{
    flex: 100% 0;
    margin-left: auto;
    height: calc(100vh - 40px);
    background-color: #fff;
    padding: 20px;
    transition: all 100ms ease;
    @include laptopUp{
        flex: calc(100% - 300px) 0;
    }
}
.note-wrapper{
    position: relative;
}
body{
    font-family: "Public Sans", sans-serif;
}
.aside-data .panel-title{
    margin: 0;
}
.section-title-01{
    margin: 0 auto 0 0;
    font-style: normal;
    font-weight: 700;
    font-size: 18px;
    text-transform: uppercase;
    color: #00AB55;
    text-align: center;
    @include tabletUp{
        text-align: left;
    }
}
.NotesAsideContainer{
    position: fixed;
    width: 100%;
    max-width: 700px;
    top: 0;
    right: -100%;
    transition: right 300ms ease;
    background-color: #fff;
    padding: 20px;
    box-shadow: rgb(145 158 171 / 20%) 0px 0px 2px 0px, rgb(145 158 171 / 12%) 0px 12px 24px -4px;
    height: 100%;
    z-index: 1;
    &.open{
        right: 0;
    }
}
.page-header-container{
    margin-bottom: 20px;
    @include tabletUp{
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
}
.text-title{
    font-family: "Segoe UI", system-ui, "Apple Color Emoji", "Segoe UI Emoji", sans-serif;
    font-weight: 700;
    font-size: 14px;
    text-transform: uppercase;
    color: #637381;
    // margin-bottom: 16px;
}