@mixin tabletUp {
    @media (min-width: 768px) {
        @content;
    }
}

@mixin laptopUp {
    @media (min-width: 1100px) {
        @content;
    }
}
@mixin mobileUp {
    @media (min-width: 590px) {
        @content;
    }
}

.line-and-bar-graph{
    .dataLegends{
        list-style: none;
    }
    .legend-container{
        display: flex;
        align-items: center;
        margin: 0;
    }
    .graphContainer{
        display: flex;
        align-items: flex-start;
        width: calc(100vw - 54px);
        overflow: auto;
        @include tabletUp{
            width: auto;
            overflow: visible;
        }
        &.has-side-text{
            padding-left: 15px;
        }
        svg{
            font-size: 10px;
            @include mobileUp{
                font-size: 12px;
            }
            @include laptopUp{
                font-size: 14px;
            }
        }
    }
    .legend{
        width: 24px;
        height: 14px;
        display: block;
        margin-right: 10px;
        font-size: 0;
    }
    .rv-xy-plot__series--line{
        fill: transparent;
    }
    .label{
        display: block;
    }
}
.dataLegends {
    padding: 20px 0 10px;
    margin: 0;
    width: 100%;
    max-width: 490px;
    height: 66px;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    list-style: none;
}