.planning-wrapper{
    display: flex;
    justify-content: space-between;
}
.PlanningContainer{
    padding: 0 20px;
    .calendar-layout{
        width: calc(100% - 500px);
    }
    .header-container{
        border-bottom: 1px solid #e5e5e5;
    }
}
.header-container{
    display: flex;
    align-items: center;
    padding: 20px 0;
    h1{
        font-family: 'Public Sans';
        font-style: normal;
        font-weight: 700;
        font-size: 18px;
        line-height: 18px;
        text-transform: uppercase;
        color: #00AB55;
        margin: 0;
    }
    .actions-wrapper{
        margin-left: auto;
    }
    button{
        margin-left: 14px;
    }
}